/*Desktop View*/
.head {
  display: flex;
  background: linear-gradient(#eceaf0, #f394d3);
  justify-content: space-between;
  align-items: center;
  padding: 0px 40px;
  font-size: 20px;
  height: 60px;
}

.head1 {
  display: flex;
  background: linear-gradient(#eceaf0, #9a8ce9);
  justify-content: space-between;
  align-items: center;
  padding: 0px 40px;
  font-size: 20px;
  height: 60px;
}

.head2 {
  display: flex;
  background: linear-gradient(#eceaf0, #9a8ce9);
  justify-content: space-between;
  align-items: center;
  padding: 0px 40px;
  font-size: 20px;
  height: 60px;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.options {
  padding-left: 25px;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 50px;
  list-style-type: none;
}
.mobile-option {
  display: none;
}

.option :hover {
  color: white;
}
.signin-up {
  display: flex;
  padding: 0px 5px;
  list-style-type: none;
}
.sign-in {
  padding-right: 50px;
  align-self: center;
}

.mobile-menu {
  display: none;
}

.style {
  display: flex;
  justify-content: space-between;
}
.style1 {
  display: list-item;
  margin-left: 10px;
}
@media (max-width: 648px) {
  /*Mobile View */
  .header {
    padding: 0px 10px;
  }

  .nav-options {
    display: flex;
    width: 100%;
    height: 350px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 0;
    transition: all 0.5s ease;
    flex-direction: column;
    list-style-type: none;
    grid-gap: 0px;
  }
  .nav-options.active {
    background: #9899d1;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    align-content: center;
    padding-left: 0px;
  }

  .menu-icon {
    width: 45px;
    height: 45px;
  }
  .option {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vw;
    padding: 30px 0px;
  }
  .sign-up {
    background: rgb(222, 9, 241);
    border-radius: 3px;
    color: white;
    padding: 20px 0px;
    width: 80%;
    align-self: center;
    border-radius: 3px;
  }

  .signin-up {
    display: none;
  }
}
.mobile-menu {
  display: block;
}
