@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

.home,
.participant,
.event {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  overflow-y: scroll;
}

/*Desktop View*/
.head {
  display: flex;
  background: linear-gradient(#eceaf0, #f394d3);
  justify-content: space-between;
  align-items: center;
  padding: 0px 40px;
  font-size: 20px;
  height: 60px;
}

.head1 {
  display: flex;
  background: linear-gradient(#eceaf0, #9a8ce9);
  justify-content: space-between;
  align-items: center;
  padding: 0px 40px;
  font-size: 20px;
  height: 60px;
}

.head2 {
  display: flex;
  background: linear-gradient(#eceaf0, #9a8ce9);
  justify-content: space-between;
  align-items: center;
  padding: 0px 40px;
  font-size: 20px;
  height: 60px;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.options {
  padding-left: 25px;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 50px;
  list-style-type: none;
}
.mobile-option {
  display: none;
}

.option :hover {
  color: white;
}
.signin-up {
  display: flex;
  padding: 0px 5px;
  list-style-type: none;
}
.sign-in {
  padding-right: 50px;
  align-self: center;
}

.mobile-menu {
  display: none;
}

.style {
  display: flex;
  justify-content: space-between;
}
.style1 {
  display: list-item;
  margin-left: 10px;
}
@media (max-width: 648px) {
  /*Mobile View */
  .header {
    padding: 0px 10px;
  }

  .nav-options {
    display: flex;
    width: 100%;
    height: 350px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 0;
    transition: all 0.5s ease;
    flex-direction: column;
    list-style-type: none;
    grid-gap: 0px;
  }
  .nav-options.active {
    background: #9899d1;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    align-content: center;
    padding-left: 0px;
  }

  .menu-icon {
    width: 45px;
    height: 45px;
  }
  .option {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vw;
    padding: 30px 0px;
  }
  .sign-up {
    background: rgb(222, 9, 241);
    border-radius: 3px;
    color: white;
    padding: 20px 0px;
    width: 80%;
    align-self: center;
    border-radius: 3px;
  }

  .signin-up {
    display: none;
  }
}
.mobile-menu {
  display: block;
}

.Navbar {
  display: grid;
  background: linear-gradient(#eeecf1, #f394d3);
  align-items: center;
  height: 60px;
  font-size: 25px;
  font-style: bold;
}

.Nav {
  display: grid;
  grid-template-columns: repeat(6, auto);
  align-items: center;
  justify-items: center;
  font-size: 25px;
}

@media (max-width: 700px) {
  .Navbar {
    display: grid;
    grid-template-areas: "logo nav";
  }

  .Nav {
    grid-area: nav;
    display: grid;
    grid-template-columns: repeat(6, auto);
    align-items: center;
    justify-items: center;
  }
}

label,
span {
  font-weight: 800;
  font-size: large;
  font-family: "Fira Sans", sans-serif;
}

.form-control {
  border-color: #167bff;
  box-sizing: inherit;
  box-shadow: none;
}

label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  font-size: 20px;
  padding-top: 10px;
  color: #d42f2f;
}

.forgot-password a {
  color: #021022;
}

.btn {
  background-color: rgb(91, 89, 250);
  border: none;
  -webkit-text-emphasis-color: white;
          text-emphasis-color: white;
  margin-left: 50px;
  padding: 10px 20px 10px 20px;
}

.root {
  width: 1200;
}

.root {
  width: 1200px;
  height: 570;
}

.cards {
  display: flex;

  justify-content: space-between;
}

